import connect from '@vkontakte/vkui-connect-promise';
import { View } from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';

import React from 'react';

import { Friends, Account, Users } from './api';

import VueFriends from './panels/Friends';
import Home from './panels/Home';

class App extends React.Component {
	constructor(props) {
		super(props);

		this.state = {
			activePanel: 'home',
			fetchedUser: null,
			access_token: 'c01d2ac272a33951f84d5ae986af906941cfc53347423ba4284cbbc69949fcda65a876ca470d6d747d6dd',
			friendsIds: null,
			userInfo: []
		};
	}

	componentDidMount() {
		connect.send("VKWebAppGetAuthToken", {"app_id": 6940370, "scope": "friends,status,wall,groups,photos,video,pages,notes,docs,stats,market"})
			.then(res => {
				this.setState({ access_token: res.data.access_token });
				this.getFriends();
			})
			.catch(e => console.error(e))
		connect.send('VKWebAppGetUserInfo', {})
			.then(res => {
				console.log('VKWebAppGetUserInfo', res)
				this.setState({ fetchedUser: res.data });
			})
			.catch(e => console.error(e))
	}

	getFriends() {
		console.log('getRequests', this.state.access_token)
		return Friends.getFriends({
			count: 5000,
			access_token: this.state.access_token
		})
			.then(data => {
				console.log('getRequests data',data);
				this.setState({ friendsIds: data.response.items });
				this.getUserInfo();
			})
			.catch(e => console.error(e));
	}
	getUserInfo () {
		const strFriendsIds = this.state.friendsIds.slice(0, 100).join(',');
		return Users.getUsers({
			user_ids: strFriendsIds,
			fields: 'photo_200_orig,status',
			access_token: this.state.access_token
		})
		.then(data => {
			this.setState((state) => {
				state.userInfo.push(...data.response);
			});
			console.log(this.state.userInfo)
		})
		.catch(e => console.error(e));
	}

	go = (e) => {
		this.setState({ activePanel: e.currentTarget.dataset.to })
	};

	render() {
		return (
			<View activePanel={this.state.activePanel}>
				<Home id="home" fetchedUser={this.state.fetchedUser} go={this.go} access_token={this.state.access_token} />
				<VueFriends id="friends" go={this.go} access_token={this.state.access_token} userInfo={this.state.userInfo} />
			</View>
		);
	}
}

export default App;
